body {
  background-color: black;
  color: black;
}

#dayLabel {
  border-radius: 10px;
  display: inline-grid;
  margin: 0 2px 0 5px;
  width: 70px;
  height: 70px;
  border: solid transparent;
  border-width: thin;
  color: white;
}

.currentDate {
  padding: 20px;
}

#dayText {
  text-align: center;
  text-indent: -10px;
  transform: translateX("-50%");
}

#dayLabel ul {
  user-select: none;
  display: inline;
  list-style: none;
  padding: 5px 0 0 5px;
  vertical-align: bottom;
}

.boxes {
  margin: 0 auto;
  width: 600px;
}

#dropdown {
  display: block;
  margin: 0 auto;
}

.month {
  color: white;
  user-select: none;
  position: absolute;
  margin-left: 10%;
  top: -15px;
  font-family: Langar, sans-serif;
  font-size: 30px;
}

.year {
  color: white;
  user-select: none;
  display: block;
  position: absolute;
  right: 100px;
  top: -15px;
  font-size: 30px;
  font-family: Langar, sans-serif;
}

/* arrows styling */

.arrowsContainer {
  position: absolute;
  top: 25px;
  margin-left: 35%;
}

.forwardArrow {
  border-radius: 20px;
  margin-left: 10px;
  user-select: none;
  position: relative;
  display: inline;
  width: 6em;
  cursor: pointer;
  background-color: transparent;
  filter: invert(1);
}

.forwardArrow:active {
  filter: saturate(50deg);
}

.saveData {
  position: absolute;
  right: 10%;
  bottom: 10%;
  width: 100px;
  height: 30px;
}

.backwardArrow {
  position: relative;
  transform: rotate(180deg);
  display: inline;
  position: relative;
  width: 6em;
  user-select: none;
  margin-right: 10px;
  border-radius: 20%;
  cursor: pointer;
  background-color: white;
  filter: invert(1);
}

.blankBox {
  user-select: none;
  border-radius: 10px;
  display: inline-grid;
  margin: 4px;
  transition: 0.2s ease-in-out;
  width: 70px;
  height: 70px;
  border: solid black;
  border-width: thin;
  color: black;
}

.blankBox ul {
  list-style: none;
  padding: 5px 0 0 5px;
  margin: 0 0 0 0;
}

.box {
  border-radius: 10px;
  display: inline-grid;
  margin: 4px;
  transition: 0.2s ease-in-out;
  width: 70px;
  height: 70px;
  background-color: black;
  border: solid white;
  border-width: thin;
  color: white;
  cursor: pointer;
  user-select: none;
}

.box ul {
  list-style: none;
  padding: 5px 0 0 5px;
  margin: 0 0 0 0;
}

.box:hover {
  background-color: green;
}

