.backwardDay {
  cursor: pointer;
  position: absolute;
  left: -100px;
  top: 40%;
  height: 100px;
  transform: rotate(180deg);
}

.forwardDay {
  cursor: pointer;
  position: absolute;
  right: -100px;
  top: 40%;
  height: 100px;
}

.modal {
  z-index: 5;
  position: fixed;
  padding-top: 50px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  position: relative;
  padding: 20px;
  margin: auto;
  margin-top: 20px;
  width: 75%;
  height: 75%;
  animation: blowUpModal 0.2s ease-in-out;
}

.modal2 {
  z-index: 10;
  /* display: none; */
  position: fixed;
  padding-top: 50px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  animation: closeUpModal 0.5s forwards;
}

.modal2-content {
  position: relative;
  padding: 20px;
  margin: auto;
  width: 75%;
  height: 75%;
}

.close-btn {
  cursor: pointer;
  float: right;
  margin-top: 20px;
  margin-right: 10px;
  color: red;
  font-size: 24px;
  font-weight: bold;
}

.close-btn:hover {
  color: darkgray;
}

@keyframes blowUpModal {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes closeUpModal {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0);
    opacity: 0;
    display: none;
  }
}

@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
